@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Courgette&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Monoton&display=swap);



 #prodCard{
  border-radius: 0px;
  border-width:0px;
  border-left: 1px solid #24222262;
  border-right: 1px solid #24222262;
  margin-bottom: 1rem;
  font-family: 'Roboto Condensed', sans-serif;
  text-align: left;
  
}


#prodCard img {
  width: 8rem;
}

#prodCard a {
  text-decoration: none;
  color: #242222;
}

#prodCard p {
  font-size: small;
}


.navbar {
  background-color: #242222;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: large;
  
}

.navbar-brand {
  color: #4BDBFF;

}

.navbar-brand:hover {
  color: #FFA14A

}

.nav-item button:hover {
  color: #FFA14A

}

.nav-item button {
  border: none;
  color: #4BDBFF;
  text-decoration: none;
  background-color: transparent; 
  padding-left: 1rem;

  
}


#cartIcon {
  color: white;
  text-decoration: none;
  display: inline-block; 
  margin-left: auto;


}



#buttons {
  border-radius: 0px;
  background-color: #242222;
  color: rgb(75, 219, 255);
  font-family: 'Courgette', cursive;
}

#buttons:hover {
  background-image: -webkit-gradient(linear, left top, right bottom, from(#242222), to(#413e3e));
  background-image: linear-gradient(to bottom right, #242222, #413e3e);
  color: white
  
}

.jumbotron img {
  object-fit: cover;
  object-position: 100% 18%; 
  max-height: 500px; 

   
  
}
.table {
  font-family: 'Roboto Condensed', sans-serif;
  text-align: left;
}


#addProdBtn{
  margin-top: auto;
}



#mern {
  font-family: 'Monoton', cursive;
}




#orderTotal{
  font-family: 'Roboto Condensed', sans-serif;
  background-color: #fae3ce;
  border-radius: 0px;
  border-width: 0px;
  height: 200px;
}

#toggler{
  border-color: transparent
}

.navbar-toggler-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(75, 219, 255)' stroke-width='3' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
  border-color:#4BDBFF
}


#text {
  position: absolute;
  z-index: 999;
  color:white;
  top: 26%;
  font-family: 'Courgette', cursive;
  font-size: 3rem
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


